body {
  margin: 0;
  padding: 0;
  font-family: "Poppins", sans-serif;
  background-color: #f5f5f5;
  box-sizing: border-box;
}

input {
  text-decoration: none;
  outline: none;
  border: none;
}

.container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

.box-container {
  width: 300px; 
  background-color: white;
  padding: 20px;
  border: 1px solid #ccc; 
  border-radius: 8px; 
  box-shadow: 3px 3px 3px rgba(0, 0, 0, 0.05);
}

header h2 {
  margin-top: 20px;
  margin-bottom: 80px;
  text-align: center;
}

.input-group {
  margin-bottom: 15px;
  text-align: left; 
  border-bottom: 1px solid rgb(201, 199, 199);
}

.submit {
  cursor: pointer;
  border-radius: 6px;
  height: 30px;
  width: 70px;
}

.submit:hover {
  background-color: black;
  color: white;
  font-weight: bolder;
}

input[type="text"],
input[type="number"],
textarea {
  width: 100%;
  padding: 8px;
  box-sizing: border-box;
}

footer {
  margin-top: 60px;
  text-align: center;
}

.button-container {
  display: flex;
  justify-content: center;
  align-items: center;
}

.button-28 {
  appearance: none;
  background-color: transparent;
  border: 2px solid #1A1A1A;
  border-radius: 15px;
  box-sizing: border-box;
  color: #3B3B3B;
  cursor: pointer;
  display: inline-block;
  font-size: 16px;
  font-weight: 600;
  line-height: normal;
  margin: 0;
  min-width: 0;
  outline: none;
  padding: 16px 24px;
  text-align: center;
  text-decoration: none;
  transition: all 300ms cubic-bezier(.23, 1, 0.32, 1);
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
  width: 30%;
  height: 30%;
  will-change: transform;
}

.button-28:disabled {
  pointer-events: none;
}

.button-28:hover {
  color: #fff;
  background-color: #1A1A1A;
  box-shadow: rgba(0, 0, 0, 0.25) 0 8px 15px;
  transform: translateY(-2px);
}

.button-28:active {
  box-shadow: none;
  transform: translateY(0);
}

@media (max-width: 768px) {
  .box-container {
    width: 80%; 
    padding: 15px;
  }

  .button-28 {
    width: 50%; 
  }
}

@media (max-width: 480px) {
  .box-container {
    width: 90%; 
    padding: 10px;
  }

  .button-28 {
    width: 70%; 
  }
}